div {
	width: 100%;
	height: 100%;
}

#container {
	width: 95%;
	min-width: 500px;
	height: 600px;
	margin: auto;
	margin-top: 4%;
	background-color: white;
	border: 5px solid slategray;
	border-radius: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	padding: 10px;
}

.ballArea {
	width: 180px;
	height: 250px;
}

.ballContainer {
	width: 15%;
	height: 100%;
	min-width: 150px;
	min-height: 150px;
	margin: auto;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

#ball {
	width: 150px;
	height: 150px;
	background-color: white;
	border: 3px solid black;
	border-radius: 50%;
	text-align: center;
	font-size: 8em;
}

button {
	margin-top: 10%;
}

.boardsArea {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.boardContainer {
	width: 45%;
	height: 350px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}

.board {
	width: 95%;
	height: 200px;
	margin: auto;
	display: flex;
	justify-content: center;
	align-items: center;
}

.playerName {
	text-align: center;
	font-size: 3em;
	width: 100%;
	height: 70px;
}

.column {
	width: 45px;
	height: 200px;
}

.card {
	width: 45px;
	height: 45px;
	background-color: rgba(204, 229, 255, 1);
	border: 1px solid rgba(0, 0, 0, 0.2);
	font-size: 2em;
	text-align: center;
}

.emptyCard {
	background-color: rgba(102, 178, 255, 1);
}

.cross {
	background-color: rgba(255, 59, 59, 0.726);
	text-decoration-line: line-through;
	text-decoration-color: rgb(255, 0, 0);
}

.hide {
	visibility: hidden;
}

.clickable:hover {
	cursor: pointer;
}
